<template>
    <div class="jsonUtils">
        <el-row :gutter="20">
            <el-col :span="4">
            </el-col>
            <el-col :span="16">
                <el-button type="primary" plain @click="cleanEscape">去除转义</el-button>
                <el-button type="success" plain @click="cleanSpace">去除空格</el-button>
            </el-col>
        </el-row>
        <el-row :gutter="20">
            <el-col :span="4">
                <div class="grid-content bg-purple"></div>
            </el-col>
            <el-col :span="8">
                <div class="grid-content bg-purple">
                    <el-input :rows="9" class="text-body" type="textarea" v-model="jsonStr"></el-input>
                </div>
            </el-col>
            <el-col :span="8">
                <json-viewer
                        :value="resultJson"
                        :expand-depth=5
                        copyable
                        boxed
                        sort></json-viewer>
            </el-col>
            <el-col :span="4">
                <div class="grid-content bg-purple"></div>
            </el-col>
        </el-row>
    </div>
</template>
<style>
    .el-col {
        margin-top: 10px;
    }

</style>

<script>
    import JsonViewer from 'vue-json-viewer'

    export default {
        name: 'jsonUtils',
        components: {
            JsonViewer,
        },
        data() {
            return {
                jsonStr: '',
                resultJson: {},
            }
        },
        watch: {
            jsonStr: function (val) {
                this.resultJson = this.formatJson(val)
            }
        },
        methods: {
            /**
             * 格式化 json
             * @param json
             * @returns {*}
             */
            formatJson: function (json) {
                try {
                    return JSON.parse(json);
                } catch (error) {
                    return {msg: '您传递的json数据格式有误，请核对...'}
                }
            },
            /**
             * 去除空格 g为全局标志
             * @param Str
             */
            cleanSpace: function () {
                this.jsonStr = this.jsonStr.replace(/\s+/g, "");
            },
            /**
             * 去除转义 g为全局标志
             */
            cleanEscape: function () {
                this.jsonStr = this.jsonStr.replace(/[\\]/g,'');
            }
        },
    }
</script>